/* global variables */
@gap: 20px; 
/* #f8f8f8 */
@primary-background: #f8f8f8; 
@mediabreakpoint:  1205px;
@coursecat-color: #999999;
@headtext-color: #333333;
@component-background: #ffffff;
@btnlike-color: #ffffff;
@texth2-color: #444444;
/* import css framework */
@import (reference) "bower_components/bootstrap/less/bootstrap.less";
/* mixins */
