.width(@num){
	width: @num*1%;
}
.margint(@p){
	margin-top: @p;
}
.marginb(@p){
	margin-bottom: @p;
}
.marginl(@p){
	margin-left: @p;
}
.marginr(@p){
	margin-right: @p;
}
/* reset css */
*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
li{
	list-style: none;
}
a{
	text-decoration: none;
}
/* typograph */
body{
  font-family: '微软雅黑',sans-serif;
  font-weight: normal;
  font-style: normal;
}
input,textarea,select,button{
  font-family: '微软雅黑',sans-serif;
}

/* layout helper classes */
.row{
  margin-left: -@gap;
  padding-left: @gap;
  .clearfix();
}
.col1-2{
  float: left;
  width: 50%;
  padding-right: @gap/2;
}
.col1-4{
  float: left;
  width: 25%;
  padding-right: @gap;
}
.row-bgap20{
	.marginb(20px);
}
/* layout */
.l-topremovable,.l-topnav,.l-banner,.l-featured-product,.l-workplace,.l-maincontent,.l-footer{
  .clearfix();
  padding-left: 0;
  padding-right: 0;
  .container{
    width: 87.5%;
    margin: 0 auto;
  }
}
/* javascript glue css selectors */
.j-coursehovered{
	display: none; /*默认为宽屏，不予以显示*/
	@media (max-width: @mediabreakpoint) {
		display: block;
		position: absolute;
		z-index: 100;
		top: 0;
		left: 0;
		bottom: 0;
		width: 200%;
		background-color: @component-background;
    margin-left: -@gap;
    padding-left: @gap;
    background-clip: content-box;
    .imagedesc{
      width: 100%;
    }
	}
}
.j-relative{
  position: relative;
}
.j-hidden{
  display: none;
}
/* page reusable components */
.m-navmenu{
  float: right;
  @font-face {
    font-family: 'applew3';
    src: url('fonts/applew3.eot');
    src:
    url('fonts/applew3.eot?#font-spider') format('embedded-opentype'),
    url('fonts/applew3.woff') format('woff'),
    url('fonts/applew3.ttf') format('truetype'),
    url('fonts/applew3.svg') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  li{
    display: inline-block;
    *display:inline;
    *zoom:1;
    a{
      font-family: "applew3",sans-serif;
      font-weight: normal;
      font-size: 16px;
      color: #666666;
      &:hover{
        color: #39a030;
      }
    }
    .search{
      width: 18px;
      height: 18px;
      background-image: url('images/search.png');
    }
  }
}

/* page non reusable components */
/* 1.login modal form */
.modallogin{
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0,0,0,0.7);
  text-align: center;
  .modalalign{
    display: inline-block;
    vertical-align: middle;
    width: 1px;
    height: 100%;
    line-height: 100%;
  }
  .modalbody{
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: 270px;
    padding-left: 40px;
    padding-right: 50px;
    background-color: @component-background;
    .close{
      position: absolute;
      right: 10px;
      top: 10px;
      cursor: pointer;
    }
    .title{
      margin-top: 30px;
      text-align: left;
      font-size: 18px;
      color: @texth2-color;
    }
    .username,.password{
      background-color: #fafafa;
      margin-bottom: 20px;
      margin-top: 20px;
      &.error{
        input{
          color: red;
          border-color: red;
        }
      }
      input{
        ::-webkit-input-placeholder { /* WebKit browsers */
            color:    #999;
        }
        :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
            color:    #999;
        }
        ::-moz-placeholder { /* Mozilla Firefox 19+ */
            color:    #999;
        }
        :-ms-input-placeholder { /* Internet Explorer 10+ */
            color:    #999;
        }
        line-height: 45px;
        font-size: 16px;
        color: #000;
      }
    }
    button{
      display: inline-block;
      width: 100%;
      margin-bottom: 45px;
      line-height: 45px;
      font-size: 16px;
      background-color: #19a138;
      color: @btnlike-color;
      cursor: pointer;
    }
    .validinfo{
      display: none;
      &.error{
        display: inline-block;
        text-align: left;
        font-size: 12px;
        color: red;
        margin: 10px;
      }
    }
  }
}
/* 2. featured product promotion */
.m-featuredprom{
  float: left;
  width: 33.3%;
  padding-right: 88px;
  color: @headtext-color;
  text-align: center;
  .badged{
    position: relative;
    float: left;
    margin-right: 18px;
    width: 80px;
    height: 80px;
    border-radius: 100%;
    background-color: #39a030;
    .alignment{
      display: inline-block;
      width: 1px;
      height: 100%;
      line-height: 100%;
      vertical-align: middle;
    }
    img{
      display: inline-block;
      vertical-align: middle;
      // position: absolute;
      // top: 50%;
      // left: 50%;
      // transform: translate(-50%,-50%);
    }

  }
  .ptext{
    overflow: hidden;
    text-align: left;
    .more{
      color: #39a030;
      font-size: 14px;
    }
  }
  .title,.description{
    color: #666666;
    font-weight: normal;
  }
  .title{
    font-size: 22px;
    margin-bottom: 1em;
  }
  .description{
    font-size: 14px;
    margin-bottom: 2em;
  }
}
/* 3. paginator */
.m-paginator{
  margin: 30px 20px 120px;
  float: right;
  .left,.right{
    display: inline-block;
    *display:inline;
    *zoom:1;
    width: 30px;
    line-height: 20px;
    text-align: center;
    background-color: #9dd8b1;
    color: @btnlike-color;
    &.disabled{
      background-color: #C7D8CD;
    }
  }
  .paginator{
    display: inline-block;
    *display:inline;
    *zoom:1;
    li{
      display: inline-block;
      *display:inline;
      *zoom:1;
      font-size: 14px;
      color: #666666;
      padding: 0 6px;
      .pitem{
        color: #666666;
      }
      &.active{
        background-color: #9dd8b1;
        .pitem{
        color: @btnlike-color;
        }
      }
    }
  }
}
/* 4.机构介绍 */
.m-hostcourseitem{
  margin: 20px;
    .clearfix();
  img{
    float: left;
    margin-right: 10px;
    width: 50px;
    height: 50px;
  } 
  .info{
    h4{
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      font-size: 14px;
      font-weight: normal;
      color: @headtext-color;
    }
    .studentnum{
      margin-top: 1em;
      font-size: 12px;
      color: @coursecat-color;
      &:before{
        display: inline-block;
        *display:inline;
        *zoom:1;
        content: url('images/person.png');
        padding-right: 5px;
        padding-left: 3px;
      }
    }
  }
}
/* 5.视频介绍组件 */
.m-videointro{
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.7);
  text-align: center;
  .mask{
    display: inline-block;
    vertical-align: middle;
    width: 1px;
    height: 100%;
    line-height: 100%;
  }
  .videocontainer{
    position: relative;
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    width: 950px;
    padding: 30px;
    line-height: 100%;
    background-color: @component-background;
    overflow: hidden;
    h2{
      margin-bottom: 20px;
      font-size: 18px;
      color: @texth2-color;
      font-weight: normal;
    }
    .closevedio{
      position: absolute;
      right: 13px;
      top: 13px;
      background-color: @component-background;
      border: none;
      cursor: pointer;
    }
  }
}
.l-topremovable{
  line-height: 36px;
  background-color: #f3f3f3;
  .container{

    font-size: 12px;
    .title{
      float: left;
      font-weight: normal;
      color: @headtext-color;
    }
    a{
      text-decoration: none;
      font-weight: normal;
      color: #21a557;
    }
    .dismiss{
      float: right;
      cursor: pointer;
      em{
        font-style: normal;
        margin-right: 6px;
      }
    }
  }
}
.l-topnav{
  line-height: 90px;
  .logo{
    margin-right: 30px;
    vertical-align: middle;
  }
  .followus{
    display: inline-block;
    *display:inline;
    *zoom:1;
    font-family: "宋体",sans-serif;
    font-weight: normal;
    font-size: 12px;
    &.j-followed{
      #follow{
        display: none;
      }
      #followed{
        display: inline-block;
        *display:inline;
        *zoom:1;

        background-color: @primary-background;
        color: @headtext-color;
        span{
          color: @coursecat-color;
          &:hover{
            color: #21a557;
            text-decoration: underline;
          }
        }
      }
    }
    #follow{
      display: inline-block;
      *display:inline;
      *zoom:1;
      background-color: #21a557;
      color: @btnlike-color;
      line-height: 24px;
      padding-left: 21px;
      padding-right: 18px;
      border-radius: 3px;
      &:before{
        content: '+';
        font-weight: bold;
        margin-right: 5px;
      }
      &:after{
        content: 'i';
        margin-left: 2px;
      }
      &:hover{
        background-color: #2fb556;
      }
    }
    #followed{
      display: none;
      line-height: 24px;
      padding-left: 21px;
      padding-right: 18px;
      &:before{
        content: '\2714';
        font-weight: bold;
        margin-right: 5px;
      }
    }
    .followernum{
      margin-left: 15px;
      em{
        font-style: normal;
      }
    }
  }
  
}
.l-banner{
  position: relative;
  .container{
    width: 100%;
  }
  .bannernav{
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 10px;
    .navpoint{
      display: inline-block;
      *display:inline;
      *zoom:1;
      width: 10px;
      height: 10px;
      bottom: 10px;
      border-radius: 10px;
      background-color: @component-background;
      cursor: pointer;
      &.active{
        background-color: #000000;
      }
    }
  }
  .m-slide{
  img{display: none;width: 100%;}
  img.active{display: inline;}
  }
}
// l-featured-product module
.l-featured-product{
  margin-top: 60px;
  
}
// workplace section 
.l-workplace{
  margin-left: -4px;
  margin-top: 60px;
  height: 220px;
  overflow: hidden;
  .workplace{
    float: left;
    width: 20%;
    height: 100%;
    padding-left: 4px;
  }
}
// main content section
.l-maincontent{
  padding-top: 80px;
  background-color: @primary-background;
  .coursecontainer{
    position: relative;
    .coursemask{
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 20%;
      background-color: rgba(0, 0, 0, 0.7);
      h1{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%);
        font-size: 64px;
        font-weight: bold;
        color: @btnlike-color;
      }
    }
    &.j-popped{
      .coursemask{
        display: none;
      }
    }
  }
  .maincontent{
    float: left;
    width: 100%; /*默认为小屏幕，当大屏时做media query*/
    @media (min-width: @mediabreakpoint) {
		width: 80%;
    }
    .tab{
      display: table-row;
      font-size: 20px;
      .tabitem{
        display: table-cell;
        text-align: center;
        width: 200px;
        line-height: 50px;
        cursor: pointer;
        background-color: @component-background;
        &.active{
          color: @btnlike-color;
          background-color: #39a030;
        }
        &.inactive{
          color: #666666;
          background-color: @component-background;
          border-bottom: ;
          box-shadow: 1px 1px 0 #d4d3d3;
        }
      }
    }
    .content{
      margin-top: 30px;
      .course{
        img{
          max-height: 108px;
        }
        position: relative;
        background-color: @primary-background;
        font-size: 12px;
        .preview{
          cursor: pointer;
          background-color: @component-background;
          padding-bottom: 10px;
          .price{
            margin-top: 10px;
          }
          .imgpreview{
            width: 100%;
          }
          .category{
            font-weight: normal;
          }
          h4,h5,p{
            margin-left: 5px;
          }
          @media (min-width: @mediabreakpoint) {
          	border: 1px solid #F1F1F1;
          }
          &:hover{
            @media (min-width: @mediabreakpoint)  {
              outline: 1px dashed #ED636E;             
              outline-offset: 10px;
            } 
          	.title{
          		color: #39A030;
          	}
          }
        }
        .detail{
          .detail-fixed{
            position: absolute;   
            z-index: 3999;         
            top: 0;
            left: @gap;
            right: 0;
            bottom: 0;
            margin-left: -@gap/2;
            padding-left: @gap/2;
            margin-top: -@gap/2;
            padding-top: @gap/2;
            outline: 2px solid #CCCACB;
          }
          .textdesc{
            margin-left: 8px;
            font-size: 12px;
            color: #666666;
            h2{

              font-weight: normal;
              margin: 4px auto 16px 0;
              font-size: 18px;
              color: @headtext-color;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;

            }
            .studynum{
               margin: 0 auto 10px 0;
             }
            img{
              width: initial;
              margin-right: 6px;
            }
            p{
              margin-top: 4px;
            }
          }
          .detailtext{
          	position: absolute;
            width: 100%;
          	bottom: 0;
            height: 84px;
            overflow: hidden;
          	padding: 24px 0;
          	margin-left: -@gap/2;
          	padding-left: @gap/2;
            padding-right: 20px;
            font-size: 14px;
            line-height: 24px;
            color: #666666;
          	background-color: @primary-background;
          }
        }
        .title{
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          font-weight: normal;
          color: @headtext-color;
        }
        .category{
          color: @coursecat-color;
          margin: 10px auto;
        }
        .studentsnum{
          display: inline-block;
          padding: 3px;
          border: 1px solid #ddd;
          border-radius: 1px;
          font-size: 5px;
          line-height: 16px;
          &:before{
            display: inline-block;
            *display:inline;
            *zoom:1;
            content: url('images/person.png');
            padding-right: 5px;
            padding-left: 3px;
          }
        }
        .price{
          font-size: 8px;
          color: #ff3f00;
          &:before{
            content: '\00A5';
            display: inline-block;
            *display:inline;
            *zoom:1;
          }
        }
      }
      
    }
  }
}
@-webkit-keyframes rotateplane {
  0% { -webkit-transform: perspective(120px) }
  50% { -webkit-transform: perspective(120px) rotateY(180deg) }
  100% { -webkit-transform: perspective(120px) rotateY(180deg)  rotateX(180deg) }
}
@keyframes rotateplane {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg)
  } 50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
    -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg)
  } 100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}
